<template>
  <div>
    <div class="list">
      <div class="item flex-between" v-for="(item,index) in data" :key="index"  @click="$router.push({path: '/help_detali',query:{article_id:item.article_id}})">
        <div class="left">
          <img src="@/assets/images/my/help_qs.png" alt="" class="img">
          <p class="ellipsis">{{item.title}}</p>
        </div>
        <van-icon name="arrow" class="arrow" />
      </div>
    </div>
    <div class="button flex-center-x">
      <div class="flex-center flex1" @click="kefu()"><img src="@/assets/images/my/help_contact.png" alt="" class="img">联系客服</div>
    </div>
  </div>
</template>

<script>
import { kefu, gethelp } from '@/axios/getData'
import { Icon, Popup, Dialog } from 'vant';
// import url from "@/axios/api.js";
// import axios from "axios";
export default {
  data(){
    return{
      data:[]
    }
  },
  components: {
    'van-icon': Icon,
    "van-popup": Popup
  },
  created() {
    this.getdata();
  },
  methods:{
    async getdata(){
      var res = await gethelp();
      this.data = res.data.article_list
      // axios.post( url.help,data ).then( res => {
      //   this.data = res.data.data.article_list
      //   console.log( this.data )
      //   // list.forEach(item => {
      //   //   this.province_list.push(item.name)
      //   //   this.province_data.push(item.id)
      //   // });
      // })
    },
    //  联系客服
    kefu(){
      var that = this;
      if(that.$store.state.kefu){
        Dialog.confirm({
          title: that.$store.state.kefu.title,
          message: that.$store.state.kefu.number
        }).then(() => {
          // 不加回报异常          
        }).catch(() =>{
          // 不加回报异常
        })
        return;
      }
      kefu().then(res =>{
        that.$store.state.kefu = res.data
        Dialog.confirm({
          title: res.data.title,
          message: res.data.number
        }).then(() => {
          // 不加回报异常          
        }).catch(() =>{
          // 不加回报异常
        })
      })
    },
  },
}
