//          this.data = res.data
//       })
//     },
//     set(item) {
//       this.show = true
//       item.content = item.content.replace(/&lt;/g,'<')
//       item.content = item.content.replace(/&gt;/g,'>')
//       item.content = item.content.replace(/&quot;/g,'"')
//       this.content = item
//     },
//     kefu() {
//       this.$post('https://api.weixin.qq.com/customservice/kfaccount/add', {access_token: this.$store.state.userInfo}).then((res) => {
       
//       })
//     }
//   }
// }
</script>

<style scoped lang="scss">
.list {
  padding: 30px;
  background: #fff;
  margin-bottom: 100px;
  .item {
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding:10pt 0;
    border-bottom: 1px solid #f2f2f2;
    font-size: 13pt;
    img {
      margin-right: 15px;
    }
    .left{
      display: flex;
    }
  }
}
.button {
  position: fixed;
  display: flex;
  align-items: center;
  bottom: 0;
  width: 100%;
  left: 0;
  padding:7pt 0;
  background: #fff;
  font-size: 13pt;
  img {
    width: 30px;
    height: 30px;
    margin-right: 10px;
  }
  div:first-child {
    border-right: 1px solid #e5e5e5;
  }
}
.explain {
  padding: 20px 30px;
  height: 40vh
}
.explain .title {
  text-align: center;
  font-size: 34px;
  margin-bottom: 20px;
}
.flex1{
  display: flex;
  justify-content:center;
  align-items: center;
  margin-left:50%;
  transform:translateX(-50%);  
}
.img{
  width:15pt;
  height:15pt;
}
</style>



